<template>
  <v-dialog
    persistent
    :value="passForm || foldForm || noteForm"
    :fullscreen="$vuetify.breakpoint.smAndDown"
    class="pt-15"
    max-width="600px"
    absolute
  >
    <v-card
      color="white"
      light
      tile
    >
      <v-toolbar color="primary" dark>
        <v-btn icon class="hidden-md-and-up" @click="toggleState()">
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
        <v-toolbar-title class="pl-2">
          {{formTitle}}
        </v-toolbar-title>
        <v-spacer></v-spacer>
        <v-tooltip
          bottom
        >
          <template v-slot:activator="{on, attrs}">
            <v-btn icon @click="toggleFormEdit()" :class="{'d-none': newForm}">
              <v-icon
                v-bind="attrs"
                v-on="on"
                class="mdi"
                :class="[formEdit ? 'mdi-pencil-off' : 'mdi-pencil']"
              >
                <!-- mdi-pencil -->
              </v-icon>
              <!-- <v-icon
                v-bind="attrs"
                v-on="on"
                v-if="!formEdit"
              >

              </v-icon> -->
            </v-btn>
          </template>
          <span>{{formEdit ? 'Close Edit Mode' : 'Edit'}}</span>
        </v-tooltip>

        <v-tooltip
          bottom
        >
          <template v-slot:activator="{on, attrs}">
            <v-btn icon class="mr-2 hidden-sm-and-down" @click="toggleState()">
              <v-icon
                v-bind="attrs"
                v-on="on"
              >
                mdi-close
              </v-icon>
            </v-btn>
          </template>
          <span>close</span>
        </v-tooltip>

      </v-toolbar>
      <v-progress-linear
        :active="loading"
        :indeterminate="loading"
        top
        color="warning"
      ></v-progress-linear>
      <PasswordForm v-if="passForm" :formData="formData"/>
      <NoteForm v-else-if="noteForm" :formData="formData"/>
      <FolderForm v-else-if="foldForm"/>
    </v-card>
  </v-dialog>
</template>

<script>
import LogoText from '@/components/LogoText'
import PasswordForm from '@/components/PasswordForm'
import NoteForm from '@/components/NoteForm'
import FolderForm from '@/components/FolderForm'
export default {
  data () {
    return {
      dialog: true,
      loading: false
    }
  },
  methods: {
    toggleFormEdit () {
      this.$store.commit('toggleFormEdit')
      // this.$store.state.formEdit = !this.$store.state.formEdit
    },
    toggleState () {
      this.$store.dispatch('toggleState')
    }
  },
  computed: {
    newForm: {
      get () {
        return this.$store.state.newForm
      },
      set () {
        return this.$store.state.newForm
      }
    },
    formEdit: {
      get () {
        return this.$store.state.formEdit
      },
      set () {
        return this.$store.state.formEdit
      }
    },
    formData () {
      return this.$store.state.formData
    },
    formTitle () {
      return this.$store.state.formTitle
    },
    passForm () {
      return this.$store.state.passForm
    },
    noteForm () {
      return this.$store.state.noteForm
    },
    foldForm () {
      return this.$store.state.foldForm
    }
  },
  components: {
    LogoText,
    PasswordForm,
    NoteForm,
    FolderForm
  }
}
</script>

<style>
  .theme--light.v-input--is-disabled textarea {
    color: black;
  }
</style>



