<template>
  <v-container>
    <v-toolbar
      flat
    >
      <v-toolbar-title v-text="this.$route.query.item || 'All Items' ">
      </v-toolbar-title>
      <v-spacer/>
    </v-toolbar>
    <ItemPanel/>
    <div style="margin-bottom: 10%"></div>
    <Dial/>
    <AddForms></AddForms>
    <v-container max-width="50">
    </v-container>
  </v-container>
</template>

<script>
import Dial from '@/components/Dial'
import ItemPanel from '@/components/ItemPanel'
import AddForms from '@/components/AddForms'
import Notifications from '@/components/Notifications'
export default {
  data () {
    return {
    }
  },
  computed: {
    urlParams () {
      var urlParams = new URLSearchParams(window.location.search)
      return urlParams
    },
    add () {
      return this.urlParams.has('add')
    },
    snackbar: {
      get () {
        return this.$store.state.snackbar
      },
      set () {
        return this.$store.state.snackbar
      }
    }
  },
  methods: {
  },
  components: {
    Dial,
    ItemPanel,
    AddForms,
    Notifications
  },
  beforeMount () {
    this.$store.dispatch('checkAllDataPresent')
  },
  mounted () {
    window.scrollTo(0, 0)
    this.$store.dispatch('loadVault')
  }
}
</script>

<style>
  .v-overlay-content {
    min-width: 320px;
  }
  .v-snack__wrapper {
    min-width: 200px;
  }
</style>



