<template>
  <v-expansion-panels
    v-model="panel"
    multiple
    :focusable="$vuetify.breakpoint.smAndDown"
  >
    <v-expansion-panel
      v-for="(folder, i) in vaultData"
      :key="folder.id"
      :readonly="$vuetify.breakpoint.smAndDown"
      v-show="counts[i]"
    >
      <v-expansion-panel-header>
          <div>
              <v-icon class="mr-10">mdi-folder-outline</v-icon>
                {{folder.folder_name}}
            <v-chip
              text-color="white"
              color="primary"
              small
              class="ml-3"
              text="5"
            >
              <b>{{counts[i]}}</b>
            </v-chip>
            <!-- <v-spacer></v-spacer> -->
            <!-- <v-btn v-if="counts[i] == '0'" icon to="Login" class="float-right" @click="deleteFolder(folder.id)">
              <v-icon >
                mdi-delete
              </v-icon>
            </v-btn> -->
          </div>
          <template v-slot:actions>
            <v-icon v-show="$vuetify.breakpoint.mdAndUp && counts[i]">$expand</v-icon>
          </template>
      </v-expansion-panel-header>
      <!-- passwords -->
      <v-expansion-panel-content
        v-for="password in folder.Passwords"
        :key="password.id"
        :class="{'mobilepad': $vuetify.breakpoint.smAndDown, 'd-none': $route.query.item != null && $route.query.item != 'Passwords'}"
      >
        <!-- <h1 v-if="$vuetify.breakpoint.smAndDown">5</h1> -->
        <MobileMenu v-bind:item="password" :type="'passwords'" :folderIndex="i" v-if="$vuetify.breakpoint.smAndDown"></MobileMenu>
        <v-toolbar v-else>
          <v-col cols="1">
          <v-icon :small="$vuetify.breakpoint.smAndDown" class="pl-0">mdi-lock</v-icon>
          </v-col>
          <v-col cols="md-5 col-lg-7">
            <v-toolbar-title>
              <v-list class="pa-0">
                <v-list-item-title>{{password.name}}</v-list-item-title>
                <v-list-item-subtitle>{{password.userName}}</v-list-item-subtitle>
              </v-list>
            </v-toolbar-title>
          </v-col>
          <v-spacer></v-spacer>
          <!-- <v-btn-toggle color="primary"> -->
          <div>
            <Options v-bind:item="password" :type="'passwords'" :folderIndex="i"></Options>
          </div>
          <!-- </v-btn-toggle> -->
          <!-- <v-toolbar-items>
            <Options v-bind:item="password" :type="'passwords'" :folderIndex="i"></Options>
          </v-toolbar-items> -->
          <!-- <v-spacer></v-spacer> -->
                            <!-- <v-toolbar-items>
                              <v-list-item :href="password.url" target="_blank" @click="!sheets">
                                <v-list-item-avatar>
                                  <v-tooltip
                                    bottom
                                  >
                                    <template v-slot:activator="{on, attrs}">
                                      <v-icon
                                        v-bind="attrs"
                                        v-on="on"
                                      >
                                        mdi-rocket-launch
                                      </v-icon>
                                    </template>
                                    <span>Launch</span>
                                  </v-tooltip>
                                </v-list-item-avatar>
                              </v-list-item>
                              <v-list-item @click="copyUsername(password.userName)">
                                <v-list-item-avatar>
                                  <v-tooltip
                                    bottom
                                  >
                                    <template v-slot:activator="{on, attrs}">
                                      <v-icon
                                        v-bind="attrs"
                                        v-on="on"
                                      >
                                        mdi-content-copy
                                      </v-icon>
                                    </template>
                                    <span>Copy Username</span>
                                  </v-tooltip>
                                </v-list-item-avatar>
                              </v-list-item>
                              <v-list-item @click="copyPassword(password.id)">
                                <v-list-item-avatar>
                                  <v-tooltip
                                    bottom
                                  >
                                    <template v-slot:activator="{on, attrs}">
                                      <v-icon
                                        v-bind="attrs"
                                        v-on="on"
                                      >
                                        mdi-content-copy
                                      </v-icon>
                                    </template>
                                    <span>Copy Password</span>
                                  </v-tooltip>
                                </v-list-item-avatar>
                              </v-list-item>
                              <v-list-item @click="x">
                                <v-list-item-avatar>
                                  <v-tooltip
                                    bottom
                                  >
                                    <template v-slot:activator="{on, attrs}">
                                      <v-icon
                                        v-bind="attrs"
                                        v-on="on"
                                      >
                                        mdi-pencil
                                      </v-icon>
                                    </template>
                                    <span>Edit</span>
                                  </v-tooltip>
                                </v-list-item-avatar>
                              </v-list-item>
                            </v-toolbar-items> -->
        </v-toolbar>
        <!-- <div v-else>Else part</div> -->
      </v-expansion-panel-content>
      <v-expansion-panel-content
        v-for="note in folder.Notes"
        :key="note.id"
        :class="{'mobilepad': $vuetify.breakpoint.smAndDown, 'd-none': $route.query.item != null && $route.query.item != 'Notes'}"
      >
        <MobileMenu :type="'notes'" :item="note" :folderIndex="i" v-if="$vuetify.breakpoint.smAndDown"></MobileMenu>
        <v-toolbar v-else>
          <v-col cols="1">
          <v-icon :small="$vuetify.breakpoint.smAndDown" class="pl-0">mdi-note</v-icon>
          </v-col>
          <v-col cols="sm10 md8 lg5">
            <v-toolbar-title>
              <v-list class="pa-0">
                <v-list-item-title>{{note.name}}</v-list-item-title>
                <!-- <v-list-item-subtitle>{{password.userName}}</v-list-item-subtitle> -->
              </v-list>
            </v-toolbar-title>
          </v-col>
          <v-spacer></v-spacer>
          <div>
            <Options :type="'notes'" :item="note" :folderIndex="i"></Options>
          </div>
        </v-toolbar>
      </v-expansion-panel-content>
    </v-expansion-panel>
    <!-- <v-container >
      <img src="../assets/EmptyBox.png" height="150" width="150" alt="">
    </v-container> -->
    <v-container fill-height v-show="isEmpty">
    <v-row justify="center" align="center" style="padding-top: 17vh">
        <h3 align="center" style="color: #596678" cols="12">
          Add Something
        </h3>
        <v-col cols="12" align="center">
          <v-btn class="ma-2" width="170px" color="#596678" dark outlined @click="toggleState({state: 'passForm', formTitle: 'Password'})" v-if="$route.query.item == 'Passwords' || $route.query.item == undefined">
            <v-icon class="pr-3">mdi-lock</v-icon>
            Password
          </v-btn>
          <v-btn class="ma-2" width="170px" color="#596678" dark outlined @click="toggleState({state: 'noteForm', formTitle: 'Note'})" v-if="$route.query.item == 'Notes' || $route.query.item == undefined">
            <v-icon class="pr-3" fab>mdi-note</v-icon>
            Note
          </v-btn>
        </v-col>
        <!-- <v-col align="center" cols="12">
          <img src="../assets/EmptyBox.png" height="150" width="150" alt="">
        </v-col> -->
    </v-row>
</v-container>
  </v-expansion-panels>
</template>

<script>
import MobileMenu from '@/components/MobileMenu'
import Options from '@/components/Options'
export default {
  data () {
    return {
      focusable: true,
      pass: false,
      test: 'Empty Folders'
    }
  },
  methods: {
    toggleState (state) {
      // this.$store.state.newForm = true
      this.$store.commit('togglenewForm', true)
      this.$store.dispatch('toggleState', state)
    }
    // deleteFolder () {
    //   return 0
    // }
  },
  computed: {
    panel: {
      get () {
        return this.$vuetify.breakpoint.smAndDown ? this.$store.state.panel : []
      },
      set () {
        return this.$vuetify.breakpoint.smAndDown ? this.$store.state.panel : []
      }
    },
    vaultData () {
      return this.$store.state.vaultData
    },
    counts () {
      var count = [0]
      this.vaultData.forEach((folder, i) => {
        count[i] = 0
        if (this.$route.query.item === undefined || this.$route.query.item === 'Passwords') {
          count[i] += folder.Passwords.length || 0
        }
        if (this.$route.query.item === undefined || this.$route.query.item === 'Notes') {
          count[i] += folder.Notes.length || 0
        }
        // count[i] = folder.Passwords.length + folder.Notes.length || '0'
      })
      return count
    },
    isEmpty () {
      return Math.max(...this.counts) === 0
    }
  },
  components: {
    MobileMenu,
    Options
  }
}
</script>

<style>
  .mobilepad > .v-expansion-panel-content__wrap {
    padding-right: 0;
    padding-left: 0;
  }
  /* .v-toolbar__content {
    padding: 0
  } */
</style>



