import Vue from 'vue'
import Vuex from 'vuex'
import DataHandler from '@/services/DataHandlers'
import AuthenticationService from '@/services/AuthenticationService'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    email: null,
    logged: false,
    home: true,
    password: false,
    addPassword: false,
    currentPage: 'home',
    currentOverlay: null,
    closeOverlay: null,
    vaultData: [],
    panel: [],
    passwordcount: 0,
    cardcount: 0,
    notecount: 0,
    drawer: false,
    mini: true,
    snackbar: {
      model: false,
      content: null,
      color: null,
      timeout: 5000
    },
    passwordForm: false,
    folders: null,
    passForm: false,
    noteForm: false,
    foldForm: false,
    formTitle: '',
    loadoverlay: false,
    formEdit: true,
    newForm: false,
    formData: undefined
  },
  mutations: {
    toggleFormEdit (store) {
      store.formEdit = !store.formEdit
    },
    togglenewForm (store, payload) {
      store.newForm = payload
    },
    toggleLogState (store) {
      store.logged = !store.logged
    },
    open: (store, payload) => {
      store[store.currentOverlay] = false
      store[store.currentPage] = false
      store[payload] = true
      store.currentPage = payload
    },
    overlay: (store, payload) => {
      store[store.currentOverlay] = null
      store[payload] = true
      store.currentOverlay = payload
    },
    loadVault: (store, payload) => {
      store.vaultData = payload
      store.panel = Array.from(Array(payload.length), (_, index) => index)
      store.folders = []
      for (var i in store.vaultData) {
        store.folders.push({'folderindex': i, 'folder_name': store.vaultData[i].folder_name})
      }
    },
    updateCount: (store, payload) => {
      store.passwordcount = payload.passwordcount
      store.notecount = payload.notecount
    },
    updateFolder: (store, payload) => {
      var folderid = payload.folderid
      var folderindex = null
      store.vaultData.find((folder, index) => {
        if (folder.id === folderid) {
          folderindex = index
        }
      })
      if (folderindex != null) {
        Vue.set(store.vaultData, folderindex, payload.folder)
      } else {
        store.vaultData.push(payload.folder)
      }
      store.vaultData.sort((x, y) => {
        var folder1 = x.folder_name.toUpperCase()
        var folder2 = y.folder_name.toUpperCase()
        if (folder1 < folder2) {
          return -1
        } else if (folder1 > folder2) {
          return 1
        }
        return 0
      })
      sessionStorage.setItem('vaultData', JSON.stringify(store.vaultData))
    },
    navDrawerMini: (store) => {
      store.mini = !store.mini
    },
    navDrawer: (store) => {
      store.drawer = !store.drawer
    }
  },
  actions: {
    toggleState: async ({state, dispatch}, payload) => {
      state.formData = undefined
      state.formEdit = true
      if (sessionStorage.getItem('encryptionKey')) {
        if (payload) {
          state[payload.state] = !state[payload.state]
          state['formTitle'] = payload.formTitle
        } else {
          state.foldForm = state.noteForm = state.passForm = false
        }
      } else {
        dispatch('logout')
      }
    },
    checkAllDataPresent ({dispatch}) {
      try {
        sessionStorage.getItem('name')
        sessionStorage.getItem('email')
        sessionStorage.getItem('encryptionKey')
      } catch (error) {
        dispatch('logout')
      }
    },
    loadVault: async ({commit}) => {
      var vaultData
      vaultData = sessionStorage.getItem('vaultData')
      vaultData = JSON.parse(vaultData)
      if (vaultData === null) {
        vaultData = await DataHandler.loadVault()
        vaultData = vaultData.data
        sessionStorage.setItem('vaultData', JSON.stringify(vaultData))
      }
      commit('loadVault', vaultData)
    },
    getCount: async ({commit}) => {
      var counts = await DataHandler.getCount()
      counts = counts.data[0]
      commit('updateCount', counts)
    },
    deleteItem: async ({state, dispatch}, payload) => {
      await DataHandler.deleteItem(payload)
        .then(() => {
          dispatch('updateFolder', {folderid: state.vaultData[payload.folderIndex].id})
          return true
        })
        .catch((error) => {
          console.log(error)
          return false
        })
    },
    updateFolder: async ({commit, dispatch}, payload) => {
      await DataHandler.updateFolder({folderid: payload.folderid})
        .then((folder) => {
          commit('updateFolder', {folderid: payload.folderid, folder: folder.data})
          dispatch('getCount')
          dispatch('loadVault')
        })
        .catch((error) => {
          console.log(error)
        })
    },
    logout: async ({state, dispatch}) => {
      state.loadoverlay = true
      try {
        var loggedOut = await AuthenticationService.logout()
        if (loggedOut) {
          sessionStorage.clear()
          document.cookie = 'connect.sid=null;expires=' + new Date(0).toUTCString
          state.loadoverlay = false
          window.location.replace('./Login')
        }
      } catch (error) {
        state.loadoverlay = false
        dispatch('logout')
        console.log(error)
      }
    }
  }
})



