<template>
  <v-container
    :class="$vuetify.breakpoint.mdAndUp ? 'fill-height' : 'py-0'"
    fluid
  >
    <!-- <link href="https://cdn.jsdelivr.net/npm/@mdi/font@latest/css/materialdesignicons.min.css" rel="stylesheet"> -->
    <v-row
      align="center"
      justify="center"
    >
      <v-col
        cols="12"
        sm="6"
        md="6"
        lg="4"
        align="center"
      >
        <v-alert
          :value="success"
          dense
          outlined
          text
          type="success"
          center
          style="width: fit-content"
          ref="successAlert"
        >
          Registration Success
        </v-alert>
        <v-alert
          :value="error"
          dense
          outlined
          text
          type="error"
          center
          style="width: fit-content"
          ref="errorAlert"
        >
          Email already Exists
        </v-alert>
        <v-card class="pb-3" :class="[$vuetify.breakpoint.smAndDown ? 'elevation-0' : 'elevation-12']">
          <v-card-title
            class="justify-center"
            :class="{'pt-0' : $vuetify.breakpoint.smAndDown}"
          >
            REGISTER
            <!-- <LogoText/> -->
          </v-card-title>
          <v-progress-linear
            :active="loading"
            indeterminate
            color="primary"
          ></v-progress-linear>
          <!-- <v-toolbar
            flat
          >
            <v-toolbar-title>
              CREATE ACCOUNT
            </v-toolbar-title>
            <v-spacer/>
            OR&nbsp;&nbsp;
            <v-toolbar-title>
              <router-link to="Login">
                LOGIN
              </router-link>
            </v-toolbar-title>
          </v-toolbar>

          <v-divider></v-divider> -->
          <v-card-text :class="[$vuetify.breakpoint.smAndDown ? 'px-2' : 'px-10']">
            <v-form :disabled="loading" ref="form" autocomplete="off">
              <v-text-field
                label="Email address"
                name="email"
                type="email"
                v-model="email"
                class="pt-0"
                :rules="emailRules"
                validate-on-blur
                ref="email"
              ></v-text-field>
              <v-text-field
                label="Name"
                name="name"
                type="text"
                v-model="name"
                :rules="nameRules"
                validate-on-blur
                ref="name"
              ></v-text-field>
              <v-menu offset-y bottom>
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    slot="activator"
                    label="Master Password"
                    name="masterpassword"
                    id="masterpassword"
                    type="password"
                    v-model="masterpassword"
                    :rules="masterpasswordRules"
                    min=0
                    max=32
                    @focus="toggleHint"
                    @blur="toggleHint"
                    v-on="on"
                    v-bind="attrs"
                    ref="masterpassword"
                  ></v-text-field>
                </template>
                <v-card v-show="hint && !passwordHint">
                  <!-- <v-card-title class="text-body-1 pb-0">Password Format:</v-card-title> -->
                  <v-card-text class="pa-0">
                    <v-list>
                      <v-list-item :class="{'pa-0': $vuetify.breakpoint.smAndDown}">
                        <v-list-item-avatar>
                          <v-icon color="green" v-if="lower">mdi-check-circle</v-icon>
                          <v-icon color="red" v-else>mdi-close-circle</v-icon>
                        </v-list-item-avatar>
                        atleast 1 Lowercase alphabet
                      </v-list-item>
                      <v-list-item :class="{'pa-0': $vuetify.breakpoint.smAndDown}">
                        <v-list-item-avatar>
                          <v-icon color="green" v-if="upper">mdi-check-circle</v-icon>
                          <v-icon color="red" v-else>mdi-close-circle</v-icon>
                        </v-list-item-avatar>
                        atleast 1 Uppercase alphabet
                      </v-list-item>
                      <v-list-item :class="{'pa-0': $vuetify.breakpoint.smAndDown}">
                        <v-list-item-avatar>
                          <v-icon color="green" v-if="digit">mdi-check-circle</v-icon>
                          <v-icon color="red" v-else>mdi-close-circle</v-icon>
                        </v-list-item-avatar>
                        atleast 1 digit
                      </v-list-item>
                      <v-list-item :class="{'pa-0': $vuetify.breakpoint.smAndDown}">
                        <v-list-item-avatar>
                          <v-icon color="green" v-if="specialchar">mdi-check-circle</v-icon>
                          <v-icon color="red" v-else>mdi-close-circle</v-icon>
                        </v-list-item-avatar>
                        atleast 1 special character
                      </v-list-item>
                      <v-list-item :class="{'pa-0': $vuetify.breakpoint.smAndDown}">
                        <v-list-item-avatar>
                          <v-icon color="green" v-if="minlength">mdi-check-circle</v-icon>
                          <v-icon color="red" v-else>mdi-close-circle</v-icon>
                        </v-list-item-avatar>
                        12 - 32 character
                      </v-list-item>
                    </v-list>
                  </v-card-text>
                </v-card>
              </v-menu>
              <v-text-field
                label="Confirm Master Password"
                name="confirmmpwd"
                type="password"
                v-model="confirmmpwd"
                :rules="confirmmpwdRules"
                validate-on-blur
                ref="confirmmpwd"
              ></v-text-field>
              <v-text-field
                label="Remainder"
                name="remainder"
                type="text"
                v-model="remainder"
                :rules="remainderRules"
                validate-on-blur
                hint="Hint to remember your password (optional)"
                ref="remainder"
              ></v-text-field>

              <v-btn
                block
                rounded
                large
                color="primary"
                @click="register"
                :disabled="loading"
                class="my-2"
              >
                Create Account
              </v-btn>
            </v-form>
          </v-card-text>
          Already have an account ? <router-link to="Login">Login</router-link>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import AuthenticationServices from '@/services/AuthenticationService'
import encrypt from '@/services/encryption'
import LogoText from '@/components/LogoText'
export default {
  name: 'Register',
  data () {
    return {
      attach: true,
      email: '',
      name: '',
      masterpassword: '',
      confirmmpwd: '',
      remainder: '',
      success: false,
      error: false,
      loading: false,
      upper: false,
      lower: false,
      digit: false,
      specialchar: false,
      minlength: false,
      hint: false,
      emailRules: [
        email => !!email || 'Email required',
        email => /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+\.[a-zA-Z]+(?:\.[a-zA-Z0-9-]+)*$/.test(email) || 'Enter valid email'
      ],
      nameRules: [
        name => !!name || 'Name required',
        name => /^[A-Za-z]+$/.test(name) || 'should contain only alphabet',
        name => (name.length >= 3 && name.length <= 50) || 'should contain 3 - 50 character'
      ],
      masterpasswordRules: [
        masterpassword => !!masterpassword || 'Password required',
        masterpassword => (this.digit = /(?=.*\d)/.test(masterpassword)),
        masterpassword => (this.upper = /(?=.*[A-Z])/.test(masterpassword)),
        masterpassword => (this.lower = /(?=.*[a-z])/.test(masterpassword)),
        masterpassword => (this.specialchar = /(?=.*[!@#$%^&*])/.test(masterpassword)),
        masterpassword => (this.minlength = masterpassword.length >= 12 && masterpassword.length <= 32)
      ],
      confirmmpwdRules: [
        confirmmpwd => !!confirmmpwd || 'Password required',
        confirmmpwd => confirmmpwd === this.masterpassword || 'Password not match'
      ],
      remainderRules: [
      ]
    }
  },
  methods: {
    toggleHint () {
      this.hint = !this.hint
    },
    async register () {
      if (!this.$refs.form.validate()) {
        return
      }
      this.loading = true
      try {
        var hashData = await encrypt.hashPassword(this.email, this.masterpassword)
        var hashConfirmData = await encrypt.hashPassword(this.email, this.confirmmpwd)
        await AuthenticationServices.register({
          email: this.email,
          name: this.name,
          masterpassword: hashData,
          confirmmpwd: hashConfirmData,
          remainder: this.remainder
        })
        this.success = true
        window.scrollTo(0, 0)
        this.loading = false
        setTimeout(() => {
          window.location.replace('../Login')
        }, 2000)
      } catch (errorResponse) {
        this.masterpassword = this.confirmmpwd = null
        var error = errorResponse.response.data
        switch (error.name) {
          case 'InputFormatError':
            this.$refs.form.validate()
            break
          case 'SequelizeUniqueConstraintError':
            this.error = true
            window.scrollTo(0, 0)
            break
          default:
            break
        }
        this.loading = false
      }
    }
  },
  computed: {
    passwordHint () {
      return this.lower && this.upper && this.digit && this.specialchar && this.minlength
    }
  },
  components: {
    LogoText
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
</style>



