<template>
    <v-form ref="form" class="pa-5" :readonly="readonly">
      <label for="url">URL :</label>
      <v-text-field
        solo
        name="url"
        type="text"
        color="white"
        v-model="formData.url"
        required
        :rules="urlRules"
        validate-on-blur
      ></v-text-field>
      <v-row>
        <v-col cols="12" md="6">
          <label for="name">NAME :</label>
          <v-text-field
            solo
            name="name"
            type="text"
            v-model="formData.name"
            :rules="nameRules"
            validate-on-blur
          ></v-text-field>
        </v-col>
        <v-col cols="12" md="6">
          <label for="folder">FOLDER :</label>
          <v-combobox
            solo
            name="folder"
            v-model="formData.folder"
            :items="folders"
            item-text="folder_name"
            item-value="folder_name"
            menu-props="auto"
            active-class="white--text"
            return-object
            :rules="folderRules"
            validate-on-blur
          >
          </v-combobox>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="12" sm="6">
          <label for="userName">USERNAME/ EMAIL :</label>
          <v-text-field
            solo
            name="userName"
            type="text"
            v-model="formData.userName"
            :rules="userNameRules"
            validate-on-blur
          ></v-text-field>
        </v-col>
        <v-col cols="12" sm="6">
          <label for="password">PASSWORD :</label>
          <v-text-field
            solo
            name="password"
            :type="show ? 'text' : 'password'"
            :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
            @click:append="show = !show"
            v-model="formData.password"
            :rules="passwordRules"
            validate-on-blur
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row
        no-gutters
        cols="12"
        v-if="formEdit"
      >
        <v-col class="text-center" cols="sm6">
          <v-btn
            color="primary"
            :disabled="loading"
            @click="toggleState({state: 'passForm'})"
          >
            Cancel
          </v-btn>
        </v-col>
        <v-col class="text-center" cols="sm6">
          <v-btn
            color="primary"
            :disabled="loading"
            @click="savePassword"
          >
            Save
          </v-btn>
        </v-col>
      </v-row>
    </v-form>
</template>

<script>
import DataHandler from '@/services/DataHandlers'
import encryption from '@/services/encryption.js'
export default {
  data () {
    return {
      height: '0.5em',
      loading: false,
      show: false,
      urlRules: [
        url => !!url || 'URL required',
        url => /(http || https):\/\/([a-zA-Z0-9])+/.test(url) || 'URL should have format like  https://www.example.com'
      ],
      nameRules: [
        name => !!name || 'Name required'
      ],
      folderRules: [
        folder => !!folder || 'Folder name required'
      ],
      userNameRules: [
        userName => !!userName || 'Username/Email required'
      ],
      passwordRules: [
        password => !!password || 'Password is required'
      ]
    }
  },
  props: {
    formData: {
      type: Object,
      default: () => {
        return {
          id: undefined,
          url: '',
          name: '',
          folder: null,
          userName: '',
          password: ''
        }
      }
    }
  },
  methods: {
    toggleState (state) {
      this.$store.dispatch('toggleState', state)
    },
    async savePassword () {
      if (!this.$refs.form.validate()) {
        return
      }
      var folderName = this.formData.folder
      var folderId = null
      if (typeof folderName === 'object') {
        folderName = folderName.folder_name
      }
      // console.log(folderName)
      // console.log(folderId)
      // console.log(folderIndex)
      this.loading = true
      const encryptionKey = sessionStorage.getItem('encryptionKey')
      const encryptedpassword = await encryption.encryptData(this.formData.password, encryptionKey)
      if (this.formData.folder === '' || this.formData.folder === null) { this.formData.folder = ':General' }
      await DataHandler.savePassword({
        id: this.formData.id,
        url: this.formData.url,
        name: this.formData.name,
        folder: folderName,
        username: this.formData.userName,
        password: encryptedpassword
      }).then((response) => {
        folderId = response.data.folder_id
        this.clearForm()
      }).catch((error) => {
        console.log(error)
      })
      this.$store.dispatch('updateFolder', {folderid: folderId})
      this.loading = false
      this.$store.dispatch('toggleState')
      this.$store.state.snackbar = {
        model: true,
        content: 'Password Added',
        color: 'primary',
        timeout: 5000
      }
    },
    clearForm () {
      if (this.$refs.form) {
        this.$refs.form.reset()
      }
    }
  },
  computed: {
    formEdit: {
      get () {
        return this.$store.state.formEdit
      },
      set () {
        return this.$store.state.formEdit
      }
    },
    readonly () {
      return !this.$store.state.formEdit
    },
    folders () {
      return this.$store.state.folders
      // return [':General', 'A', 'Z']
      // Array.from(Array(this.$store.state.vaultData), (folder) => folder.folder_name)
    }
  },
  components: {
  }
}
</script>

<style>

</style>



