<template>
  <v-app-bar
    color="white"
    :class="[$vuetify.breakpoint.xl ? 'xlp' : '']"
    clipped-left
    clipped-right
    app
  >
    <v-app-bar-nav-icon @click="navDrawer" class="hidden-md-and-up"></v-app-bar-nav-icon>
    <v-spacer class="hidden-md-and-up" ></v-spacer>
    <v-toolbar-title class="mr-8"><LogoText/></v-toolbar-title>
    <v-spacer></v-spacer>
    <nav v-if="logged">
      <v-btn
        @click="logout"
        class="hidden-sm-and-down"
        color="primary"
      >
        Logout
      </v-btn>
    </nav>
    <nav class="hidden-sm-and-down" v-else>
          <v-btn
            text
            to="Register"
            exact
            active-class="white--text"
          >
            Register
          </v-btn>
          <v-btn
            text
            to="Login"
            exact
            active-class="white--text"
          >
            Login
          </v-btn>
    </nav>
  </v-app-bar>
</template>

<script>
import LogoText from '@/components/LogoText'
export default {
  data () {
    return {}
  },
  methods: {
    async logout () {
      this.$store.dispatch('logout')
    },
    navDrawer () {
      this.$store.commit('navDrawer')
    }
  },
  computed: {
    logged: {
      get () {
        return this.$store.state.logged
      },
      set () {
        return this.$store.state.logged
      }
    }
  },
  components: {
    LogoText
  }
}
</script>

<style scoped>
  .v-btn--active {
    background-color: #00a8f3;
  }
  .xlp {
    padding: 0 20% 0 20%;
  }
</style>



