<template>
  <v-navigation-drawer
    v-model="drawer"
    clipped
    app
    :permanent="permanent"
    mobile-breakpoint="10000"
    :class="[$vuetify.breakpoint.xl ? 'xll' : '']"
  >
    <v-list v-if="logged">
      <v-list-item class="px-2" two-line>
        <v-btn icon class="mr-3" style="background-color: black" large dark><h2>{{name.charAt(0).toUpperCase()}}</h2></v-btn>
        <v-list-item-content>
        <v-list-item-title>{{name}}</v-list-item-title>
        <v-list-item-subtitle>{{email}}</v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
      <v-divider></v-divider>
      <v-list-item-group
        active-class="white--text"
      >
        <v-list-item
          :to="{name: 'Vault'}"
          exact
        >
          <v-list-item-avatar>
            <v-icon active-class="white--text">
              mdi-home
            </v-icon>
          </v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title>
              HOME
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-list-item
          :to="{name: 'Vault', query: {item: 'Passwords'}}"
          exact
        >
          <v-list-item-avatar>
            <v-icon>
              mdi-lock
            </v-icon>
          </v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title>
              PASSWORDS
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-list-item
          :to="{name: 'Vault', query: {item: 'Notes'}}"
          exact
        >
          <v-list-item-avatar>
            <v-icon>
              mdi-note
            </v-icon>
          </v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title>
              NOTES
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <!-- <v-list-item
          :to="{name: 'Vault', query: {item: 'Cards'}}"
          exact
        >
          <v-list-item-avatar>
            <v-icon>
              mdi-credit-card
            </v-icon>
          </v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title>
              CARDS
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item> -->
        <!-- <v-list-item
          to="settings"
          exact
        >
          <v-list-item-avatar>
            <v-icon>mdi-cog</v-icon>
          </v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title>
              SETTINGS
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item> -->
        <v-list-item @click="logout()">
          <v-list-item-avatar>
            <v-icon>mdi-logout-variant</v-icon>
          </v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title>
              LOGOUT
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list-item-group>
    </v-list>
    <v-list v-else class="pt-0">
      <v-banner dark color="primary" class="mb-2" two-line>
          <h1 class="pt-4">Get your OnePass account now</h1>
          <template v-slot:actions>
            <v-btn
              rounded
              to="Register"
              dark
              color="#fc7c2c"
            >
              Register
            </v-btn>
          </template>
        </v-banner>
      <v-list-item-group>
        <v-list-item to="Index" exact>
          <v-list-item-avatar>
            <v-icon>mdi-home</v-icon>
          </v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title>Home</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-list-item to="Login" exact>
          <v-list-item-avatar>
            <v-icon>mdi-login-variant</v-icon>
          </v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title>Login</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-list-item to="About" exact>
          <v-list-item-avatar>
            <v-icon>mdi-information</v-icon>
          </v-list-item-avatar>
          <v-list-item-content>
            <v-list-item-title>About</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list-item-group>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
import LogoText from '@/components/LogoText'
// import store from '@/store/store'
export default {
  name: 'NavigatinDrawer',
  data () {
    return {
    }
  },
  props: {
    title: String
  },
  methods: {
    open (payload) {
      this.$store.commit('open', payload)
    },
    navDrawerMini () {
      this.$store.commit('navDrawerMini')
    },
    logout () {
      this.$store.dispatch('logout')
    }
  },
  computed: {
    email () {
      var email = sessionStorage.getItem('email')
      if (email) {
        return email
      } else {
        this.$store.dispatch('logout')
      }
    },
    name () {
      var name = sessionStorage.getItem('name')
      if (name) {
        return name
      } else {
        this.$store.dispatch('logout')
      }
    },
    drawer: {
      get () {
        return this.$store.state.drawer && this.$vuetify.breakpoint.smAndDown
      },
      set () {
        return this.$store.state.drawer && this.$vuetify.breakpoint.smAndDown
      }
    },
    logged: {
      get () {
        return this.$store.state.logged
      },
      set () {
        return this.$store.state.logged
      }
    },
    permanent: {
      get () {
        return this.logged && this.$vuetify.breakpoint.mdAndUp
      },
      set () {
        return this.logged && this.$vuetify.breakpoint.mdAndUp
      }
    }
  },
  components: {
    LogoText
  },
  watch: {
  }
}
</script>

<style scoped>
  .v-list-item-group .v-list-item--active {
    background-color: #00a8f3;
    color: white;
  }
  .v-icon.v-icon {
    justify-content: left;
  }
  .user_detail {
    background-color: primary;
    color: white;
    padding: clamp(50px, 6vw, 100px);
  }
</style>



