<template>
  <v-footer
    padless
    :fixed="logged"
    color="black"
    class="white--text"
    :app="logged"
  >
    <v-col class="margin" v-if="['/index', '/'].includes($route.path.toLowerCase())">
      <v-row cols="12" dark class="text-body-2 text-sm-h6 text-md-h6 text-lg-h6">
        <v-col class="col-12 col-sm-6 col-md-6 col-lg-6 my-16">
            <span class="mb-5">Contact</span>
            <br>
            <a href = "mailto: krishgokul351@gmail.com"><v-icon dark>mdi-email</v-icon>&nbsp;&nbsp;krishgokul351@gmail.com</a>
            <br>
            <a href="tel:+91 7339556554"><v-icon dark>mdi-phone</v-icon>  +91 7339556554</a>
            <br>
            <img v-show="$vuetify.breakpoint.smAndUp" width="70%" src="../assets/calling.svg" alt="">
        </v-col>
        <v-col class="col-12 col-sm-6 col-md-6 col-lg-6">
          <v-form :class="{'ma-16': $vuetify.breakpoint.mdAndUp}" ref="reportForm">
            <div class="mb-1">Report Bug / Issue</div>
            <br>
            <v-alert
              color="white"
              outlined
              align="center"
              v-model="success"
            >
              Bug / Issue Reported. Thanks for your help
            </v-alert>
            <label for="email">Email</label>
            <v-text-field
              dark
              solo
              name="email"
              type="email"
              v-model="email"
              validate-on-blur
              :rules="emailRules"
              @click="success = false"
            ></v-text-field>
            <label for="description">Description</label>
            <v-textarea
              dark
              color="white"
              solo
              name="description"
              v-model="description"
              :rules="descriptionRules"
              hint="describe bug or issue. maximum of 200 words"
              @click="success = false"
            ></v-textarea>
            <v-btn dark class="float-right" :loading="loading" @click="report">
              Report
            </v-btn>
          </v-form>
        </v-col>
      </v-row>
    </v-col>
    <v-col
      class="text-center z-pad"
      cols="12"
      v-else
    >
      &copy; {{ new Date().getFullYear() }} - OnePass
    </v-col>
  </v-footer>
</template>

<script>
import DataHandler from '@/services/DataHandlers'
export default {
  data: () => ({
    email: '',
    description: '',
    loading: false,
    icons: [
      'mdi-facebook',
      'mdi-twitter',
      'mdi-linkedin',
      'mdi-instagram'
    ],
    emailRules: [
      email => !!email || 'Email required',
      email => /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+\.[a-zA-Z]+(?:\.[a-zA-Z0-9-]+)*$/.test(email) || 'Enter valid Email'
    ],
    descriptionRules: [
      description => !!description || 'Description required',
      description => (description == null || description.split(' ').length <= 200) || 'Please describe briefly. max 200 words'
    ],
    success: false
  }),
  methods: {
    // toggleSuccessAlert () {

    // },
    async report () {
      this.loading = true
      if (!this.$refs.reportForm.validate()) {
        this.loading = false
        return
      }
      try {
        await DataHandler.submitReport({
          email: this.email,
          description: this.description
        })
        this.loading = false
        this.$refs.reportForm.reset()
        this.success = true
      } catch (error) {
        this.loading = false
        console.log(error)
      }
    }
  },
  computed: {
    logged () {
      return this.$store.state.logged
    }
  }
}
</script>

<style scoped>
  .z-pad {
    padding: 0 0 0 0;
  }
  .v-application {
    line-height: 0.2;
  }
  .margin {
    margin: 1% 5% 0% 3%;
  }
</style>



