<template>
  <v-container
    :class="$vuetify.breakpoint.mdAndUp ? 'fill-height' : 'pt-0'"
    fluid
  >
    <v-row
      align="center"
      justify="center"
    >
      <v-col
        cols="12"
        sm="8"
        md="6"
        lg="4"
        align="center"
      >
        <v-alert
          :value="error"
          dense
          outlined
          text
          style="width: fit-content"
          type="error"
        >
          Invalid Email / Password
        </v-alert>
        <v-card :class="[$vuetify.breakpoint.smAndDown ? 'elevation-0' : 'elevation-12']" class="pb-4">
          <v-card-title
            class="justify-center"
          >
            <!-- <LogoText/> -->
            LOGIN
            <!-- <h3 style="color: primary">Login</h3> -->
          </v-card-title>
          <v-progress-linear
            :active="loading"
            indeterminate
            color="primary"
          ></v-progress-linear>
          <!-- <v-toolbar
            flat
          >
            <v-toolbar-title>
              LOGIN
            </v-toolbar-title>
            <v-spacer/>
            OR&nbsp;&nbsp;
            <v-toolbar-title>
              <router-link to="Register">
                CREATE AN ACCOUNT
              </router-link>
            </v-toolbar-title>
          </v-toolbar> -->

          <!-- <v-divider></v-divider> -->
          <v-card-text :class="[$vuetify.breakpoint.smAndDown ? 'px-2' : 'px-10']">
            <v-form :disabled="loading">
              <v-text-field
                label="Email address"
                name="email"
                type="text"
                v-model="email"
                @click="toggleErrorMsg"
              ></v-text-field>
              <v-text-field
                label="Master Password"
                name="masterpassword"
                type="password"
                v-model="masterpassword"
                @click="toggleErrorMsg"
              ></v-text-field>
              <br>
              <v-btn
                block
                rounded
                large
                color="primary"
                @click="login"
                :disabled="loading"
              >
                Login
              </v-btn>
            </v-form>
          </v-card-text>
          Don't have an account ? <router-link to="Register">Register</router-link>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import AuthenticationServices from '@/services/AuthenticationService'
import encrypt from '@/services/encryption'
import LogoText from '@/components/LogoText'
export default {
  name: 'Login',
  data () {
    return {
      email: '',
      masterpassword: '',
      error: false,
      loading: false
    }
  },
  methods: {
    async login () {
      this.loading = true
      try {
        var hashData = await encrypt.hashPassword(this.email, this.masterpassword)
        var response = await AuthenticationServices.login({
          email: this.email,
          masterpassword: hashData
        })

        var encryptionSalt = response.data.encryptionSalt
        await sessionStorage.setItem('encryptionKey', encrypt.hashPassword(encryptionSalt, this.masterpassword))
        sessionStorage.setItem('email', response.data.email)
        sessionStorage.setItem('name', response.data.name)
        this.masterpassword = ''
        this.$store.state.logged = true
        this.loading = false
        this.$router.push({name: 'Vault'})
      } catch (error) {
        this.masterpassword = ''
        this.loading = false
        this.error = true
        window.scrollTo(0, 0)
      }
    },
    toggleErrorMsg () {
      this.error = false
    }
  },
  components: {
    LogoText
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
/* a {
  color: primary;
  text-decoration: none;
} */
</style>



