<template>
  <v-form class="pa-5" style="height: inherit" ref="form" :readonly="readonly">
    <v-row cols="12" >
    <v-col cols="12" md="5">
      <label for="name">NAME :</label>
      <v-text-field
        solo
        name="name"
        type="text"
        v-model="formData.name"
        :rules="nameRules"
        validate-on-blur
      ></v-text-field>
      <label for="folder">FOLDER :</label>
      <v-combobox
        solo
        name="folder"
        v-model="formData.folder"
        :items="folders"
        item-text="folder_name"
        item-value="folder_name"
        menu-props="auto"
        return-object
        :rules="folderRules"
        validate-on-blur
      ></v-combobox>
    </v-col>
    <v-col cols="12" md="7">
      <label for="note">NOTE :</label>
      <v-textarea
        solo
        name="note"
        v-model="formData.content"
        :rules="contentRules"
        validate-on-blur
      ></v-textarea>
    </v-col>
    </v-row>
    <v-row
      no-gutters
      cols="12"
      v-if="formEdit"
    >
      <v-col class="text-center" cols="sm6">
        <!-- @click="overlay('closeOverlay')" -->
        <v-btn
          @click="toggleState({state: 'noteForm'})"
          color="primary"
          :disabled="loading"
        >
          Cancel
        </v-btn>
      </v-col>
      <v-col class="text-center" cols="sm6">
        <v-btn
          @click="saveNote"
          color="primary"
          :disabled="loading"
        >
          Save
        </v-btn>
      </v-col>
    </v-row>
  </v-form>
</template>

<script>
import DataHandler from '@/services/DataHandlers'
export default {
  data () {
    return {
      folders: this.$store.state.folders,
      loading: false,
      nameRules: [
        name => !!name || 'Name required'
      ],
      folderRules: [
        folder => !!folder || 'Folder required'
      ],
      contentRules: [
      ]
    }
  },
  props: {
    formData: {
      type: Object,
      default: () => {
        return {
          id: undefined,
          name: '',
          folder: null,
          content: ''
        }
      }
    }
  },
  methods: {
    toggleState (state) {
      this.$store.dispatch('toggleState', state)
    },
    async saveNote () {
      if (!this.$refs.form.validate()) {
        return
      }
      var folderName = this.formData.folder
      var folderId = null
      if (typeof folderName === 'object') {
        folderName = folderName.folder_name
      }
      this.loading = true
      if (this.formData.folder === '' || this.formData.folder === null) { this.formData.folder = ':General' }
      await DataHandler.saveNote({
        id: this.formData.id,
        name: this.formData.name,
        folder: folderName,
        note: this.formData.content
      })
        .then((response) => {
          folderId = response.data.folder_id
          this.clearForm()
        })
        .catch((error) => {
          console.log(error)
        })
      this.$store.dispatch('updateFolder', {folderid: folderId})
      this.loading = false
      this.$store.dispatch('toggleState')
      this.$store.state.snackbar = {
        model: true,
        content: 'Note Added',
        color: 'primary',
        timeout: 5000
      }
    },
    clearForm () {
      if (this.$refs.form) {
        this.$refs.form.reset()
      }
    }
  },
  computed: {
    formEdit: {
      get () {
        return this.$store.state.formEdit
      },
      set () {
        return this.$store.state.formEdit
      }
    },
    readonly () {
      return !this.$store.state.formEdit
    }
  }
}
</script>

<style>

</style>



