<template>
  <v-bottom-sheet v-model="sheets">
    <template v-slot:activator="{ on, attrs }">
      <v-toolbar row cols="12" v-bind="attrs" v-on="on" v-if="type == 'passwords'">
        <v-col cols="1">
          <v-icon :small="$vuetify.breakpoint.smAndDown" class="pl-0">mdi-lock</v-icon>
        </v-col>
        <v-col cols="11">
          <v-toolbar-title style="overflow: hide">
            <v-list class="pa-0">
              <v-list-item-title>{{item.name}}</v-list-item-title>
              <v-list-item-subtitle>{{item.userName}}</v-list-item-subtitle>
            </v-list>
          </v-toolbar-title>
        </v-col>
        <v-spacer></v-spacer>
      </v-toolbar>
      <v-toolbar row cols="12" v-bind="attrs" v-on="on" v-else-if="type == 'notes'">
        <v-col cols="1">
          <v-icon :small="$vuetify.breakpoint.smAndDown" class="pl-0">mdi-note</v-icon>
          </v-col>
          <v-col cols="11">
            <v-toolbar-title>
              <v-list class="pa-0">
                <v-list-item-title>{{item.name}}</v-list-item-title>
                <!-- <v-list-item-subtitle>{{password.userName}}</v-list-item-subtitle> -->
              </v-list>
            </v-toolbar-title>
          </v-col>
          <v-spacer></v-spacer>
      </v-toolbar>
    </template>
    <v-list @click="!sheets" class="pl-4">
      <v-subheader v-if="type == 'password'">{{password.name}}</v-subheader>
      <v-subheader v-else-if="type == 'note'">{{note.name}}</v-subheader>
        <Options @closeSheet="closeSheet" v-bind:item="item" v-bind:type="type" :folderIndex="folderIndex"></Options>
    </v-list>
  </v-bottom-sheet>
</template>

<script>
import Options from '@/components/Options'
export default {
  data () {
    return {
      sheets: false
    }
  },
  props: {
    item: {
      type: Object
    },
    type: {
      type: String
    },
    folderIndex: {
      type: Number
    }
  },
  methods: {
    closeSheet () {
      this.sheets = false
    }
  },
  components: {
    Options
  }
}
</script>

<style scoped>
  .row {
    margin-left: -16px;
  }
  .v-btn--active {
    background-color: #00a8f3;
  }
  .v-btn--active > span > .v-icon {
    color: white;
  }
  .mobilepad > .v-expansion-panel-content__wrap {
    padding-right: 0;
    padding-left: 0;
  }
  .v-toolbar__content {
    padding: 0
  }
</style>



