// src/plugins/vuetify.js

import Vue from 'vue'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'

const vuetify = new Vuetify({
  theme: {
    themes: {
      light: {
        primary: '#00a8f3'
      }
    }
  }
})

Vue.use(Vuetify)

export default vuetify



