<template>
  <v-layout :class="{'list': $vuetify.breakpoint.smAndDown}" :row="$vuetify.breakpoint.smAndDown">
    <!-- Launch Button -->
    <v-tooltip
      bottom
      v-if="type == 'passwords'"
    >
      <template v-slot:activator="{on, attrs}">
        <v-btn
          text
          :x-large="$vuetify.breakpoint.smAndDown"
          :icon="$vuetify.breakpoint.mdAndUp"
          justify="left"
          @click="toggleSheet(item.url)"
        >
          <v-icon
            v-bind="attrs"
            v-on="on"
          >
            mdi-rocket-launch
          </v-icon>
          <span class="d-md-none">&nbsp;&nbsp;Launch</span>
        </v-btn>
      </template>
      <span>Launch</span>
    </v-tooltip>

    <!-- Copy Button -->
    <v-menu
      offset-y
      v-if="type == 'passwords'"
    >
      <template v-slot:activator="{ on: menu, attrs }">
        <v-tooltip
          bottom
        >
          <template v-slot:activator="{on: tooltip}">
            <v-btn
              text
              :x-large="$vuetify.breakpoint.smAndDown"
              :icon="$vuetify.breakpoint.mdAndUp"
              v-on="menu"
            >
              <v-icon v-bind="attrs" v-on="tooltip">mdi-content-copy</v-icon>
              <span class="d-md-none">&nbsp;&nbsp;Copy</span>
            </v-btn>
          </template>
          <span>copy</span>
        </v-tooltip>
      </template>
      <v-list class="pa-0">
        <v-list-item>
          <v-btn
            text
            :x-large="$vuetify.breakpoint.smAndDown"
            @click="copyUsername(item.userName)"
            class="py-1"
          >
            <v-icon class="mr-2">
              mdi-account-outline
            </v-icon>
            Copy userName
          </v-btn>
        </v-list-item>
        <v-list-item>
          <v-btn
            text
            :x-large="$vuetify.breakpoint.smAndDown"
            @click="copyPassword(item.id)"
            class="py-1"
          >
            <v-icon class="mr-2">
              mdi-lock
            </v-icon>
            Copy Password
          </v-btn>
        </v-list-item>
      </v-list>
    </v-menu>

    <!-- Open button -->
    <v-btn
      text
      :icon="$vuetify.breakpoint.mdAndUp"
      :x-large="$vuetify.breakpoint.smAndDown"
      @click="setFormData(item)"
    >
      <v-tooltip
        bottom
      >
        <template v-slot:activator="{on, attrs}">
          <v-icon
            v-bind="attrs"
            v-on="on"
          >
            mdi-book-open
          </v-icon>
          <span class="d-md-none">&nbsp;&nbsp;Open</span>
        </template>
        <span>Open</span>
      </v-tooltip>
    </v-btn>

    <!-- Delete Button -->
    <v-btn
      text
      :icon="$vuetify.breakpoint.mdAndUp"
      :x-large="$vuetify.breakpoint.smAndDown"
      @click="confirmdelete(item.id, item.name, folderIndex, type)"
    >
      <v-tooltip
        bottom
      >
        <template v-slot:activator="{on, attrs}">
          <v-icon
            v-bind="attrs"
            v-on="on"
          >
            mdi-delete
          </v-icon>
          <span class="d-md-none">&nbsp;&nbsp;Delete</span>
        </template>
        <span>Delete</span>
      </v-tooltip>
    </v-btn>

    <!-- pop up dialog -->
    <v-dialog
      v-model="dialog.model"
      persistent
      max-width = "500"
    >
      <v-card>
        <v-card-title class="headline" color="primary" style="background-color:#00a8f3; color:white">{{dialog.title}}</v-card-title>

        <v-card-text class="pa-3" style=""><h3>{{dialog.message}}</h3></v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="primary"
            text
            @click="dialog.model = false"
          >
            Cancel
          </v-btn>

          <v-btn
            color="error"
            text
            @click="dialog.action(dialog.itemId, dialog.type, dialog.folderIndex)"
          >
            Delete
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
import DataHandlers from '@/services/DataHandlers'
import encryption from '@/services/encryption'
export default {
  data () {
    return {
      dialog: {
        model: false,
        title: null,
        text: null,
        action: null,
        folder: null,
        item: null
      }
    }
  },
  props: {
    item: {
      type: Object
    },
    type: {
      type: String
    },
    folderIndex: {
      type: Number
    }
  },
  computed: {
  },
  methods: {
    toggleSheet (url) {
      this.$emit('closeSheet')
      window.open(url)
    },
    async setFormData (formData) {
      this.$emit('closeSheet')
      var password = null
      if (this.type === 'passwords') {
        await DataHandlers.getPassword({passwordid: formData.id})
          .then((encryptedPassword) => {
            password = encryptedPassword.data.encryptedPassword
          })
          .catch((error) => {
            console.log(error)
          })
        var key = sessionStorage.getItem('encryptionKey')
        password = await encryption.decryptData(password, key)
        formData.password = password
      }
      formData.folder = this.$store.state.vaultData[this.folderIndex].folder_name
      var formTitle = this.type
      formTitle = formTitle.charAt(0).toUpperCase() + formTitle.slice(1, formTitle.length - 1)
      this.$store.commit('togglenewForm', false)
      this.$store.dispatch('toggleState', {state: this.type.slice(0, 4) + 'Form', formTitle: formTitle})
      this.$store.state.formEdit = false
      this.$store.state.formData = formData
    },
    copyUsername (username) {
      navigator.clipboard.writeText(username)
        .then(() => {
          this.$emit('closeSheet')
          this.$store.state.snackbar = {
            model: true,
            color: 'primary',
            content: 'Username copied',
            timeout: 5000
          }
        }).catch((error) => {
          console.log(error)
        })
    },
    async copyPassword (passwordId) {
      var password = null
      await DataHandlers.getPassword({passwordid: passwordId})
        .then((encryptedPassword) => {
          password = encryptedPassword.data.encryptedPassword
        })
        .catch((error) => {
          console.log(error)
        })
      var key = sessionStorage.getItem('encryptionKey')
      password = await encryption.decryptData(password, key)
      navigator.clipboard.writeText(password)
        .then(() => {
          this.$emit('closeSheet')
          this.$store.state.snackbar = {
            model: true,
            color: 'primary',
            content: 'Password copied',
            timeout: 5000
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    async deleteItem (itemid, itemtype, folderIndex) {
      await this.$store.dispatch('deleteItem', {itemid, itemtype, folderIndex})
        .then(() => {
          this.dialog = {
            model: false
          }
          this.$store.state.snackbar = {
            model: true,
            content: 'Item Deleted successfully',
            color: 'primary',
            timeout: 5000
          }
        })
    },
    confirmdelete (id, name, folderIndex, type) {
      this.$emit('closeSheet')
      this.dialog = {
        title: 'Delete Item',
        message: 'Are you sure you want to delete ' + name + ' ?',
        action: this.deleteItem,
        model: true,
        itemId: id,
        type: type,
        folderIndex: folderIndex
      }
    }
  }
}
</script>

<style scoped>
  /* .theme--light.v-btn-toggle:not(.v-btn-toggle--group) .v-btn.v-btn {
    border-color: rgba(0,0,0,0)!important;
    border-top-color: rgba(0, 0, 0, 0) !important;
    border-right-color: rgba(0, 0, 0, 0) !important;
    border-bottom-color: rgba(0, 0, 0, 0) !important;
    border-left-color: rgba(0, 0, 0, 0) !important;
  }
  .theme--light.v-btn.v-btn--icon {
    color: rgba(0,0,0,0);
  } */
  .v-btn--active {
    background-color: white;
  }
  .v-list-item {
    padding: 0
  }
  .v-icon {
    color: '#00a8f3';
  }
  .v-btn:not(.v-btn--round).v-size--x-large {
    padding: 0 90% 0 5%;
  }
</style>



